.postContent {
    > h2 {
        @apply text-2xl font-bold mb-5;
    }

    > h3 {
        @apply text-xl font-bold mb-4;
    }

    > p,
    > ol,
    > ul {
        @apply mt-4;

        & + h2 {
            @apply mt-8;
        }

        & + h3 {
            @apply mt-6;
        }
    }

    > ol,
    > ul {
        @apply mb-4 pl-9;
    }

    > ol {
        @apply list-decimal;
    }

    > ul {
        @apply list-disc;
    }

    a {
        text-decoration: underline;
        color: #43b2ff;
    }
}
